export default {
	name: 'number-box',
	props: {
		value: {
			type: Number,
			default: 1
		},
		min: {
			type: Number,
			default: -Infinity
		},
		max: {
			type: Number,
			default: Infinity
		},
		step: {
			type: Number,
			default: 1
		}
	},
	computed: {
		disableSubtract() {
			return this.value <= this.min
		},
		disableAdd() {
			return this.value >= this.max
		}
	},
	onUnload(){
		this.value = 1,
		this.step = 1,
		this.max = Infinity,
		this.min = -Infinity;
	},
	methods: {
		subtract(evt) {
			this._handleStep('subtract');
		},
		add(evt) {
			this._handleStep('add');
		},
		_handleStep(type) {
			let value = this.value;
			if (type === 'subtract') {
				value -= this.step
			} else if (type === 'add') {
				value += this.step
			}
			if (value < this.min || value > this.max) {
				return
			}
			this.value = value
			this.$parent.getPrice();
		},
		handleBlur(evt) {
			let value = evt.detail.value;
			if (!value) {
				this.value = 1;
				return
			}
			value = +value;
			if (value > this.max) {
				value = this.max;
			} else if (value < this.min) {
				value = this.min
			}
			this.value = value
			this.$parent.getPrice();
		}
	}
}
